export default function filter() {
  console.log('event.target', event.target)
  const cards = document.querySelectorAll('.goods .card') // –∫–∞—Ä—Ç–æ—á–∫–∞ —Ç–æ–≤–∞—Ä–∞
  const discountCheckbox = document.getElementById('discount-checkbox')
  const min = document.getElementById('min')
  const max = document.getElementById('max')
  const activLi = document.querySelector('.catalog-list li.active')

  const searchInput = document.querySelector('.search-wrapper_input') // input –¥–ª—è –ø–æ–∏—Å–∫–∞ –ø–æ —Å–∞–π—Ç—É
  const searchText = new RegExp(searchInput.value.trim(), 'i')

  cards.forEach((card) => {
    const cardPrice = card.querySelector('.card-price')
    // console.dir(cardPrice);
    const price = parseFloat(cardPrice.textContent)
    const discount = card.querySelector('.card-sale')
    const cardTitle = card.querySelector('.card-title')
    card.parentNode.style.display = 'flex'

    if ((min.value && price < min.value) || (price > max.value && max.value)) {
      // console.log('price: ', price)
      card.parentNode.style.display = 'none'
    } else if (discountCheckbox.checked && !discount) {
      // console.log('discont: ', price)
      card.parentNode.style.display = 'none'
    } else if (activLi && (card.dataset.category !== activLi.textContent)) {
      // console.log('category: ', price)
      card.parentNode.style.display = 'none'
    } else if (!searchText.test(cardTitle.textContent)) { // –µ—Å—Ç—å –ª–∏ —Ä–µ–≥—É–ª—è—Ä–Ω–æ–µ –≤—ã—Ä–∞–∂–µ–Ω–∏–µ –≤ –∑–∞–≥–æ–ª–æ–≤–∫–µ –∫–∞—Ä—Ç–æ—á–∫–∏
      // console.log('text: ', price)
      card.parentNode.style.display = 'none' // —Å–∫—Ä—ã–≤–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–µ –ø–æ–ø–∞–≤—à–∏–µ –≤ –¥–∏–∞–ø–æ–∑–æ–Ω –ø–æ–∏—Å–∫–∞
    } else {
      // console.log('show: ', price)
      card.parentNode.style.display = 'flex' // –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    }
  })
}
